.main-container__title {
  text-align: center;
  margin-bottom: 70px;
}
.main-title-span {
  color: #008000;
}
.our-benefits {
  display: flex;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 70px;
  width: fit-content;
}
.price__title {
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 25px;
}
.connaction-form {
  display: flex;
  flex-direction: column;
  width: 400px;
  margin: 0 auto;
}
.connaction-form__input {
  padding: 10px 5px;
  font-size: 20px;
  border: 1px solid #9e9e9e;
  border-radius: 5px;
}
.connaction-form__input:not(:last-child) {
  margin-bottom: 10px;
}
.connaction-form__input::placeholder {
  color: #9e9e9e;
  font-size: 20px;
}
.connaction-form__submit {
  margin-top: 15px;
  cursor: pointer;
  text-align: center;
  font-size: 20px;
  color: #ffffff;
  background-color: #008000;
  padding: 15px 5px;
  box-sizing: border-box;
  max-width: 100%;
  border-radius: 5px;
}
